@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Positions the week days on the week page */
.flex {
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-around;
  padding: 2%;
}

/* Week day on the week page */
.WeekDay {
  flex-grow: 1;
}

/* Format the daily tasks */
.tasks {
  text-align: left;
  padding-left: 6%;
  padding-bottom: 10%;
  color: #92bdc7;
  font-weight: bold;
}

/* Format the progress bar */
progress[value] {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 90%;
  height: 40px;
  margin-bottom: 1%;
}

progress[value]::-webkit-progress-value {
  background-color: #92bdc7;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  
}

progress[value]::-webkit-progress-bar {
  background-color: #eee;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
  /* border: solid 1px #92bdc7; */
}

.week-days {
  min-width: 0;
  width: 12%;
  min-height: 500px;
  border: solid 1px #92bdc7;
  border-radius: 10px;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
}

/* On hover of the week days */
.week-days:hover {
  width: 16%;
  /* border: solid 4px #92bdc7; */
  /* border-radius: 10px; */
}

.week-days h3 { 
    padding: 15px;
    margin: auto;
    display: block;
    color: #757373;
    font-weight: 500;
}



/* Style the accountabubble title text */
h2 {
  padding: 2%;
  color: #3B93A6;
  font-weight: bold;
  text-align: left;
}

/* Style the group of tasks per person */
.task_group {
  padding-bottom: 5%;
}

.tasks {
  font-size: 13px;
  font-weight: 500;
}

.assignee {
  color: #92bdc7;
  font-weight: 500;
  border: solid 1px #92bdc7;
  padding: 3%;
  margin: 6%;
  font-size: 18px;
  padding-left: 8%;
  text-align: left;
}


/* Style the current day */
.selected-day h3, .selected-day .tasks, .selected-day .assignee { 
  color: white;
}

.selected-day .assignee {
  border: solid 1px white;
  box-shadow: 3px 3px white;
}

.selected-day {
  color: white;
  background-color: #92bdc7;
  width: 18%;
}

/* On hover of the week days */
.selected-day:hover {
  width: 19%;
  border: solid 4px #92bdc7;
  border-radius: 10px;
}

.week-days .add-task:hover {
  position: absolute;
  bottom: 2%;
  color: white;
  font-style: italic;
  border: solid 1px white;
  background-color: #92bdc7;
  border-radius: 10px;
  padding: 5%;
  left: 10%;
  right: 10%;
  cursor: pointer;
}

/* Position the add task button */
.week-days .add-task, .selected-day .add-task:hover  {
  position: absolute;
  bottom: 2%;
  color: #92bdc7;
  font-style: italic;
  border: solid 1px #92bdc7;
  background-color: white;
  border-radius: 10px;
  padding: 5%;
  left: 10%;
  right: 10%;
  cursor: pointer;
}

/* Position the add task button */
.selected-day .add-task {
  position: absolute;
  bottom: 2%;
  color: white;
  font-style: italic;
  border: solid 1px white;
  background-color: #92bdc7;
  border-radius: 10px;
  padding: 5%;
  left: 10%;
  right: 10%;
  cursor: pointer;
}
